.App {
  text-align: center;
  box-sizing: border-box;
  background-color: rgba(249, 249, 249, 0.949);

  .chatGpt {
    display: flex;
    height: 100vh;

    .left {
      flex: 0.2;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      border-right: 3px rgb(179, 174, 174) solid;
      background-color: rgb(49, 45, 45);
      color: aliceblue;


      .hide {

        position: absolute;
        top: 28%;
        left: 60%;
        padding: 5px 10px;
        width: 80%;
        border-radius: 5px;
        font-size: small;
        text-align: justify;
        z-index: -1;
        color: #222222;
        background-color: rgba(249, 249, 249, 0.949);
        box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;

      }

      .mode {
        position: relative;

        .modeText:hover+.hide {
          z-index: 1;
        }
      }

      .length {
        position: relative;

        .lengthText:hover+.hide {
          z-index: 1;
        }
      }

      .temperature {
        position: relative;

        .tempText:hover+.hide {
          z-index: 1;
        }
      }





      select {
        width: 70%;
        height: 28px;
        border-radius: 10px;
      }


      .avatar {
        display: flex;

        img {
          border-radius: 50%;
          margin: 0 10px 0 10px;
          opacity: 0.8;

          &:hover {
            width: 120px;
            height: 120px;
          }
        }
      }

      footer {
        padding: 20px 0 20px 0;
      }

    }



    .right {
      flex: 0.8;
      display: flex;
      flex-direction: column;
      padding-top: 30px;


      .chatLog {
        flex: 0.85;
        padding-left: 10%;
        overflow: scroll;

        img {
          border-radius: 50%;
        }

        p {
          font-family: Arial, Helvetica, sans-serif;
          font-size: large;
          font-weight: 600;
          color: rgb(51, 52, 53);
          padding: 0 20px 0 20px;
        }


        .message1 {
          display: flex;
          align-items: center;
          width: 80%;
          background: rgba(210, 219, 225, 0.524);
          border-radius: 10px 10px 10px 10px;
          margin-top: 20px;
          padding: 10px;
          text-align: left;


        }

        .message2 {
          display: flex;
          justify-content: flex-end;
          align-items: center;
          width: 80%;
          background: rgb(241, 227, 212);
          border-radius: 10px 10px 10px 10px;
          margin-top: 20px;
          padding: 5px;
        }

      }

      .form {
        flex: 0.15;
        padding-top: 30px;
        align-self: center;
        width: 80%;


        input {
          height: 40x;
          width: 80%;
          max-width: 700px;
          font-size: 1.3rem;
          border-radius: 10px;
          padding: 5px;
        }

        button {
          height: 40px;
          width: 40px;
          background-color: rgb(36, 35, 35);
          color: white;
          border: 1px solid #222222;
          border-radius: 10px;
          font-weight: 600;
          font-size: 1.5rem;
          margin-left: 20px;
          cursor: pointer;

          &:hover {
            background-color: white;
            color: rgb(36, 35, 35);

          }

          margin-top: 10px;

        }
      }

    }

  }

  @media (max-width:768px) {



    .chatGpt {
      flex-direction: column;

      .left {

        .hide {
          left: 10%;
        }

      }

    }





  }

}